






































import Vue from 'vue';
import {Component, Prop} from 'vue-property-decorator';
import List from '@/components/Labels/AddLabel/List.vue';

@Component({components: {List}})
export default class Tags extends Vue {
  view1 = 'visible';
  view2 = '';
  view3 = '';
  view4 = '';
  view5 = '';
  view = 'view1';
  view2X = 'left';
  view3X = 'left';
  view4X = 'left';
  startX = 0;
  endX = 0;
  moveX = 0;
  select = '';
  @Prop() optionalTags!: string[];

  mounted() {
    const tag = document.querySelector('.tag');
    (tag as HTMLElement).addEventListener('touchstart', e => {
      this.startX = (e as TouchEvent).changedTouches[0].pageX;
    });
    (tag as HTMLElement).addEventListener('touchend', e => {
      this.endX = (e as TouchEvent).changedTouches[0].pageX;
      this.moveX = this.endX - this.startX;
      if (this.optionalTags.length >= 21) {
        if (this.moveX > 0) {
          if (this.view2 !== '') {
            this.view1 = 'visible';
            this.view2 = '';
            this.view = 'view1';
            this.view2X = 'left';
          } else if (this.view3 !== '') {
            this.view2 = 'visible';
            this.view3 = '';
            this.view = 'view2';
            this.view3X = 'left';
          } else if (this.view4 !== '') {
            this.view3 = 'visible';
            this.view4 = '';
            this.view = 'view3';
            this.view4X = 'left';
          } else if (this.view5 !== '') {
            this.view4 = 'visible';
            this.view5 = '';
            this.view = 'view4';
          }
        } else if (this.moveX < 0) {
          if (this.view1 !== '') {
            this.view2 = 'visible';
            this.view1 = '';
            this.view = 'view2';
          } else if (this.view2 !== '') {
            this.view3 = 'visible';
            this.view2 = '';
            this.view = 'view3';
            this.view2X = 'right';
          } else if (this.view3 !== '') {
            this.view4 = 'visible';
            this.view3 = '';
            this.view = 'view4';
            this.view3X = 'right';
          } else if (this.view4 !== '') {
            this.view5 = 'visible';
            this.view4 = '';
            this.view = 'view5';
            this.view4X = 'right';
          }
        }
      } else if (this.optionalTags.length >= 16) {
        if (this.moveX > 0) {
          if (this.view2 !== '') {
            this.view1 = 'visible';
            this.view2 = '';
            this.view = 'view1';
            this.view2X = 'left';
          } else if (this.view3 !== '') {
            this.view2 = 'visible';
            this.view3 = '';
            this.view = 'view2';
            this.view3X = 'left';
          } else if (this.view4 !== '') {
            this.view3 = 'visible';
            this.view4 = '';
            this.view = 'view3';
          }
        } else if (this.moveX < 0) {
          if (this.view1 !== '') {
            this.view1 = '';
            this.view2 = 'visible';
            this.view = 'view2';
          } else if (this.view2 !== '') {
            this.view2 = '';
            this.view3 = 'visible';
            this.view = 'view3';
            this.view2X = 'right';
          } else if (this.view3 !== '') {
            this.view3 = '';
            this.view4 = 'visible';
            this.view = 'view4';
            this.view3X = 'right';
          }
        }
      } else if (this.optionalTags.length >= 11) {
        if (this.moveX > 0) {
          if (this.view2 !== '') {
            this.view1 = 'visible';
            this.view2 = '';
            this.view = 'view1';
            this.view2X = 'left';
          } else if (this.view3 !== '') {
            this.view2 = 'visible';
            this.view3 = '';
            this.view = 'view2';
          }
        } else if (this.moveX < 0) {
          if (this.view1 !== '') {
            this.view1 = '';
            this.view2 = 'visible';
            this.view = 'view2';
          } else if (this.view2 !== '') {
            this.view2 = '';
            this.view3 = 'visible';
            this.view = 'view3';
            this.view2X = 'right';
          }
        }
      } else if (this.optionalTags.length >= 6 && this.optionalTags.length <= 10) {
        if (this.moveX > 0) {
          if (this.view2 !== '') {
            this.view1 = 'visible';
            this.view2 = '';
            this.view = 'view1';
            this.view2X = 'left';
          }
        } else if (this.moveX < 0) {
          if (this.view1 !== '') {
            this.view1 = '';
            this.view2 = 'visible';
            this.view = 'view2';
          }
        }
      }
    });
  }

  selectFn(item: string) {
    if (this.select === item) {
      this.select = '';
      this.$emit('update:chosenTag', '');
    } else {
      this.select = item;
      this.$emit('update:chosenTag', item);
    }
  }
}
