














import Vue from 'vue';
import {Component, Prop} from 'vue-property-decorator';
import AddLabel from '@/components/Labels/AddLabel.vue';
import RemoveLabel from '@/components/Labels/RemoveLabel.vue';
import Tags1 from '@/components/Labels/Tags1.vue';
import Tags2 from '@/components/Labels/Tags2.vue';
import Tip from '@/components/Labels/Tip.vue';

@Component({
  components: {
    AddLabel, RemoveLabel, Tags1, Tags2, Tip
  }
})
export default class Labels extends Vue {
  value = '';
  keys = '';
  visible = false;
  @Prop() view!: string;
  @Prop() tab!: string;
  @Prop() tags!: {};
  @Prop() selectedList!: string[];
  @Prop() tabChanged!: Function;
  @Prop() emptyList!: Function;
  @Prop() removeData!: Function;
  @Prop() optionalTags!: string[];
  @Prop() addData!: Function;
  @Prop() helper!: boolean;
  @Prop() back!: Function;

  addConfirm(e: []) {
    this.addData(e);
    this.visible = true;
    setTimeout(() => {
      this.visible = false;
    }, 1500);
  }

  change(tab: string) {
    this.tab = tab;
  }

  removeBounce(value: string, keys: string) {
    this.$emit('update:view', 'RemoveLabel');
    this.value = value;
    this.keys = keys;
  }

  removeConfirm() {
    this.removeData(this.keys);
  }

  addBounce() {
    this.$emit('update:view', 'AddLabel');
  }
}
