
















import Vue from 'vue';
import Mark from '@/components/Money/Note/Mark.vue';
import Amount from '@/components/Money/Note/Amount.vue';
import Button from '@/components/Money/Note/Button.vue';
import DatePicker from '@/components/Money/Note/DatePicker.vue';
import Warning1 from '@/components/Money/Note/Warning1.vue';
import Warning2 from '@/components/Money/Note/Warning2.vue';
import Warning3 from '@/components/Money/Note/Warning3.vue';
import {Component, Prop, Watch} from 'vue-property-decorator';

@Component({
  components: {
    DatePicker, Mark, Amount, Button, Warning1, Warning2, Warning3
  }
})
export default class Note extends Vue {
  mark = '';
  date = '';
  amount = '';
  @Prop() fn2!: Function;
  @Prop() fn1!: Function;
  @Prop() visible1!: boolean;
  @Prop() visible2!: boolean;
  @Prop() visible3!: boolean;

  @Watch('mark')
  onMarkChanged() {
    this.$emit('update:mark', this.mark);
  }

  @Watch('date', {immediate: true})
  onDateChanged() {
    this.$emit('update:date', this.date);
  }

  @Watch('amount')
  onAmountChanged() {
    this.$emit('update:amount', this.amount);
  }

}
