
















import Vue from 'vue';
import {Component, Prop, Watch} from 'vue-property-decorator';
import model from '@/model';
import DatePicker from '@/components/Statistic/DatePicker.vue';
import Chart from '@/components/Statistic/Chart.vue';
import Detail from '@/components/Statistic/Detail.vue';
import Tip from '@/components/Statistic/Tip.vue';

@Component({
  components: {Tip, Detail, DatePicker, Chart}
})
export default class Statistic extends Vue {
  view = '';
  date = new Date();
  component = 'Chart';
  chart = 'visible';
  detail = '';
  type = '支出';
  amount = 0;
  visible1 = false;
  visible2 = false;
  startY = 0;
  endY = 0;
  moveY = 0;
  statistic = false;
  recordList: RecordItem[] = model.fetch('recordList');
  data1 = model.fetch('data1');
  data2 = model.fetch('data2');

  @Prop() tab!: string;
  @Prop() tabChanged!: Function;
  @Prop() emptyList!: Function;

  @Watch('tab')
  onTabChanged() {
    this.type = this.type === '支出' ? '收入' : '支出';
  }

  mounted() {
    const clientHeight = document.documentElement.clientHeight - 138.8 + 'px';
    const layoutWrapper = document.querySelector('.layout-wrapper');
    const slideBox = document.createElement('div');
    slideBox.style.position = 'fixed';
    slideBox.style.width = '100vw';
    slideBox.style.height = clientHeight;
    slideBox.style.top = '92.2px';
    slideBox.style.left = '0';
    (layoutWrapper as HTMLElement).appendChild(slideBox);
    const chart = document.querySelector('.chart');
    for (const item of [slideBox, chart]) {
      (item as HTMLElement).addEventListener('touchstart', e => {
        this.startY = (e as TouchEvent).changedTouches[0].pageY;
      });
      (item as HTMLElement).addEventListener('touchend', e => {
        this.endY = (e as TouchEvent).changedTouches[0].pageY;
        this.moveY = this.endY - this.startY;
        if (this.moveY > 10) {
          this.component = 'Chart';
          this.chart = 'visible';
          this.detail = '';
        }
        if (this.moveY < -10) {
          this.component = 'Detail';
          this.visible2 = false;
          this.chart = '';
          this.detail = 'visible';
          if (this.statistic) this.visible1 = false;
        }
      });
    }
  }

  get option() {
    if (this.component === 'Detail') return {};
    this.amount = 0;
    const {data1} = this;
    const {data2} = this;
    const data = [];
    for (let i = 0; i < this.recordList.length; i++) {
      const currentItem = this.recordList[i];
      const currentAmount = currentItem.amount;
      const currentMonth = currentItem.date.slice(5, 7);
      if (currentMonth === this.month && currentItem.tab === this.tab) {
        for (let j = 0; j < currentItem.selectedList.length; j++) {
          const currentTag = currentItem.selectedList[j];
          const currentTagName = currentTag in data1 ? data1[currentTag] : data2[currentTag];
          if (data.length === 0) {
            data.push({value: currentAmount, name: currentTagName});
            continue;
          }
          let mark = false;
          for (let k = 0; k < data.length; k++) {
            const currentItem = data[k];
            if (currentTagName === currentItem.name) {
              currentItem.value += currentAmount;
              mark = true;
            }
          }
          if (!mark) data.push({value: currentAmount, name: currentTagName});
        }
        this.amount += currentAmount;
      }
    }
    this.visible1 = data.length === 0;
    this.visible2 = data.length !== 0;
    return {
      color: [
        '#FF8D78',
        '#FFAA78',
        '#FFD578',
        '#FFFC78',
        '#78FFB2',
        '#78FFEC',
        '#78D9FF',
        '#7887FF'
      ],
      tooltip: {
        trigger: 'item'
      },
      legend: {
        left: 'center'
      },
      series: [
        {
          type: 'pie',
          radius: ['26%', '70%'],
          itemStyle: {
            borderRadius: 10,
            borderColor: '#fff',
            borderWidth: 2
          },
          label: {
            show: false,
            position: 'center'
          },
          emphasis: {
            label: {
              show: true,
              fontSize: '24',
              fontWeight: 'bold',
              color: '#FF8D78'
            }
          },
          labelLine: {
            show: false
          },
          data: data
        }
      ]
    };
  }

  get month() {
    const month = this.date.getMonth() + 1;
    if (month < 10) return '0' + month;
    return month;
  }
}
