





















import Vue from 'vue';
import {Component, Prop} from 'vue-property-decorator';
import Tags from '@/components/Labels/AddLabel/Tags.vue';
import Warning1 from '@/components/Labels/AddLabel/Warning1.vue';
import Warning2 from '@/components/Labels/AddLabel/Warning2.vue';

@Component({
  components: {
    Tags, Warning1, Warning2
  }
})
export default class AddLabel extends Vue {
  newName = '';
  chosenTag = '';
  mark = '';
  visible1 = false;
  visible2 = false;
  @Prop() back!: Function;
  @Prop() optionalTags!: string[];

  confirm() {
    if (this.newName !== '' && this.chosenTag !== '') {
      const name = this.newName;
      let nameLength = 0;
      let num;
      for (let i = 0; i < name.length; i++) {
        num = name.charCodeAt(i);
        if (num > 0 && num <= 127) {
          nameLength++;
        } else {
          nameLength += 2;
        }
      }
      if (nameLength > 8) {
        this.visible2 = true;
        (document.querySelector('#btn2') as HTMLButtonElement).disabled = true;
        setTimeout(() => {
          this.visible2 = false;
          (document.querySelector('#btn2') as HTMLButtonElement).disabled = false;
        }, 1500);
      } else {
        this.$emit('update:confirm', [this.chosenTag, this.newName]);
      }
    } else {
      this.visible1 = true;
      (document.querySelector('#btn2') as HTMLButtonElement).disabled = true;
      setTimeout(() => {
        this.visible1 = false;
        (document.querySelector('#btn2') as HTMLButtonElement).disabled = false;
      }, 1500);
    }
  }
}
