





















import Vue from 'vue';
import {Component, Prop, Watch} from 'vue-property-decorator';
import model from '@/model';

@Component
export default class Detail extends Vue {
  recordList: RecordItem[] = model.fetch('recordList');
  data1 = model.fetch('data1');
  data2 = model.fetch('data2');
  statistic: { title: string; items: RecordItem[] }[] = [];
  visible = false;
  @Prop() month!: string;

  @Watch('month', {immediate: true})
  onMonthChanged() {
    this.statistic = [];
    const {statistic} = this;
    const sortedList = this.recordList.sort((a, b) => +b.date.replace(/-/g, '') - +a.date.replace(/-/g, ''));
    for (let i = 0; i < sortedList.length; i++) {
      const currentItem = sortedList[i];
      const currentMonth = currentItem.date.slice(5, 7);
      if (currentMonth === this.month) {
        if (statistic.length === 0) {
          statistic.push({title: currentItem.date, items: [currentItem]});
          continue;
        }
        const lastItem = statistic[statistic.length - 1];
        if (currentItem.date === lastItem.title) lastItem.items.push(currentItem);
        else statistic.push({title: currentItem.date, items: [currentItem]});
      }
    }
    this.visible = statistic.length === 0;
    if (statistic.length > 0) this.$emit('update:statistic', true);
  }

  tagString(tag: string) {
    const {data1} = this;
    const {data2} = this;
    if (tag in data1) {
      return data1[tag];
    } else {
      return data2[tag];
    }
  }
}
