


















import Vue from 'vue';
import {Component, Prop} from 'vue-property-decorator';
import Note from '@/components/Money/Note.vue';
import Tags1 from '@/components/Money/Tags1.vue';
import Tags2 from '@/components/Money/Tags2.vue';
import Button from '@/components/Money/Button.vue';
import Warning from '@/components/Money/Warning.vue';
import Confirm from '@/components/Money/Confirm.vue';
import model from '@/model';

@Component({
  components: {
    Confirm,
    Warning, Note, Tags1, Tags2, Button
  }
})
export default class Money extends Vue {
  view = '';
  date = '';
  mark = '';
  amount = '';
  record: RecordItem = {
    tab: '', selectedList: [], date: '', mark: '', amount: 0
  };
  recordList: RecordItem[] = model.fetch('recordList');
  visible1 = false;
  visible2 = false;
  visible3 = false;
  visible4 = false;
  confirm = false;
  @Prop() selectedList!: string[];
  @Prop() tab!: string;
  @Prop() tabChanged!: Function;
  @Prop() data1!: {};
  @Prop() data2!: {};
  @Prop() tags!: string[];
  @Prop() emptyList!: Function;

  bounce() {
    if (this.selectedList && this.selectedList.length >= 1) {
      this.view = 'Note';
    } else {
      this.visible2 = true;
      (document.querySelector('#btn1') as HTMLButtonElement).disabled = true;
      setTimeout(() => {
        this.visible2 = false;
        (document.querySelector('#btn1') as HTMLButtonElement).disabled = false;
      }, 1500);
    }
  }

  back() {
    this.view = '';
  }

  commit() {
    const {mark} = this;
    let markLength = 0;
    let num;
    for (let i = 0; i < mark.length; i++) {
      num = mark.charCodeAt(i);
      if (num > 0 && num <= 127) {
        markLength++;
      } else {
        markLength += 2;
      }
    }
    if (markLength > 88) {
      this.visible3 = true;
      (document.querySelector('#btn3') as HTMLButtonElement).disabled = true;
      setTimeout(() => {
        this.visible3 = false;
        (document.querySelector('#btn3') as HTMLButtonElement).disabled = false;
      }, 1500);
      return;
    }
    if (this.date !== '' && this.amount !== '') {
      if (parseFloat(this.amount) <= 0) {
        this.visible4 = true;
        (document.querySelector('#btn3') as HTMLButtonElement).disabled = true;
        setTimeout(() => {
          this.visible4 = false;
          (document.querySelector('#btn3') as HTMLButtonElement).disabled = false;
        }, 1500);
        return;
      }
      this.record.tab = this.tab;
      this.record.selectedList = this.selectedList;
      this.record.date = this.date;
      this.record.mark = this.mark;
      this.record.amount = parseFloat(this.amount);
      const record2 = model.clone(this.record);
      this.recordList.push(record2);
      model.save('recordList', this.recordList);
      this.back();
      this.emptyList();
      this.date = '';
      this.mark = '';
      this.amount = '';
      this.confirm = true;
      setTimeout(() => {
        this.confirm = false;
      }, 1500);
    } else {
      this.visible1 = true;
      (document.querySelector('#btn3') as HTMLButtonElement).disabled = true;
      setTimeout(() => {
        this.visible1 = false;
        (document.querySelector('#btn3') as HTMLButtonElement).disabled = false;
      }, 1500);
    }
  }
}

