





import Vue from 'vue';
import {Component, Prop, Watch} from 'vue-property-decorator';
import {EChartsOption, EChartsType} from 'echarts';
import * as echarts from 'echarts';

@Component
export default class Chart extends Vue {
  chart!: EChartsType;
  @Prop() option!: EChartsOption;
  @Prop() month!: string;
  @Prop() tab!: string;

  mounted() {
    this.chart = echarts.init(this.$refs.wrapper as HTMLDataElement);
    this.chart.setOption(this.option as EChartsOption);
  }

  @Watch('month')
  onMonthChanged() {
    this.chart.dispose();
    this.chart = echarts.init(this.$refs.wrapper as HTMLDataElement);
    this.chart.setOption(this.option as EChartsOption);
  }

  @Watch('tab')
  onMontTabChanged() {
    this.chart.dispose();
    this.chart = echarts.init(this.$refs.wrapper as HTMLDataElement);
    this.chart.setOption(this.option as EChartsOption);
  }
}
