











import Vue from 'vue';
import {Component, Prop, Watch} from 'vue-property-decorator';

@Component
export default class Tip extends Vue {
  visible1 = false;
  visible2 = false;
  @Prop() component!: string;
  @Prop() visible!: boolean;

  @Watch('component', {immediate: true})
  onComponentChanged() {
    if (this.component === 'Chart' && !this.visible) {
      this.visible1 = false;
      this.visible2 = false;
      setTimeout(() => {
        this.visible1 = true;
      }, 1000);
      setTimeout(() => {
        this.visible2 = true;
      }, 2100);
    }
  }
}
