









import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import model from '@/model';

@Component
export default class App extends Vue {
  dataSource: {} = model.fetch('dataSource');
  data1: {} = model.fetch('data1');
  data2: {} = model.fetch('data2');
  optionalTags: string[] = [];
  tab = 'Tags1';
  selectedList: string[] = [];
  helper = true;
  view = '';

  back() {
    this.view = '';
  }

  created() {
    for (const key in this.dataSource) {
      if (Object.keys(this.data1).indexOf(key) < 0 && Object.keys(this.data2).indexOf(key) < 0) {
        this.optionalTags.push(key);
      }
    }
  }

  onDataChanged() {
    this.optionalTags = [];
    for (const key in this.dataSource) {
      if (Object.keys(this.data1).indexOf(key) < 0 && Object.keys(this.data2).indexOf(key) < 0) {
        this.optionalTags.push(key);
      }
    }
    this.helper = !this.helper;
  }

  change(tab: string) {
    this.tab = tab;
    this.selectedList = [];
  }

  emptyList() {
    this.selectedList = [];
  }

  removeData(key: string) {
    if (this.tab === 'Tags1') {
      delete this.data1[key];
      this.back();
      model.save('data1', this.data1);
    } else {
      delete this.data2[key];
      this.back();
      model.save('data2', this.data2);
    }
    this.onDataChanged();
  }

  addData(newTag: string[]) {
    const key = newTag[0];
    const value = newTag[1];
    if (this.tab === 'Tags1') {
      this.data1[key] = value;
      model.save('data1', this.data1);
    } else {
      this.data2[key] = value;
      model.save('data2', this.data2);
    }
    this.back();
    this.onDataChanged();

  }
}
