






















import Vue from 'vue';
import {Component, Prop, Watch} from 'vue-property-decorator';

@Component
export default class Tags1 extends Vue {
  realTags = {};
  @Prop() tags!: {};
  @Prop() removeBounce!: Function;
  @Prop() addBounce!: Function;
  @Prop() helper!: boolean;

  @Watch('helper', {immediate: true})
  onHelperChanged() {
    this.realTags = {};
    this.realTags = this.tags;
  }
}
