













import Vue from 'vue';
import {Component, Prop} from 'vue-property-decorator';

@Component
export default class Layout extends Vue {
  @Prop() tab!: string;
  @Prop() tabChanged!: Function;
  @Prop() view!: string;
  @Prop() layout!: string;
  @Prop() list!: string;
  @Prop() emptyList!: Function;

  mounted() {
    const node = document.querySelector('.layout-wrapper');
    (node as HTMLElement).addEventListener('touchmove', e => {
      e.preventDefault();
    });
  }

  changeLayout() {
    this.emptyList();
    this.tabChanged('Tags1');
  }
}
