












import Vue from 'vue';
import {Component, Prop, Watch} from 'vue-property-decorator';

@Component
export default class List extends Vue {
  circle1 = 'selected';
  circle2 = '';
  circle3 = '';
  circle4 = '';
  circle5 = '';
  @Prop() view!: string;
  @Prop() optionalTags!: string[];

  @Watch('view', {immediate: true})
  onViewChanged(value: string) {
    if (value === 'view1') {
      this.circle1 = 'selected';
      this.circle2 = '';
    } else if (value === 'view2') {
      this.circle2 = 'selected';
      this.circle1 = '';
      this.circle3 = '';
    } else if (value === 'view3') {
      this.circle3 = 'selected';
      this.circle2 = '';
      this.circle4 = '';
    } else if (value === 'view4') {
      this.circle4 = 'selected';
      this.circle3 = '';
      this.circle5 = '';
    } else {
      this.circle5 = 'selected';
      this.circle4 = '';
    }
  }

}
